.App {
  display: flex;
  justify-content: center;
  align-items: center;
}

button.modal-toggle,
input[type='submit'] {
  background-color: turquoise;
  cursor: pointer;
  padding: 1rem 2rem;
  text-transform: uppercase;
  border: none;
}

button.modal-toggle:not(:first-child) {
  margin-left: 10px;
}

.form-group {
  margin-top: 10px;
}

input[type='text'],
input[type='password'],
input[type='email'] {
  box-sizing: border-box;
  width: 100%;
  padding: 0.5rem 0.7rem;
}